<template>
    <div class="selection_container">
        <div :class="['loader_container', { loading: !playerHasSelected }]">
            <LoaderComponent v-if="!playerHasSelected" />
            <h3 v-else>The Other Player Has Already Selected</h3>
        </div>
        <div class="input_container">
            <input
                autofocus
                @keydown="eraser"
                @keypress="writer"
                id="number_chooser"
                maxlength="4"
                placeholder="Type Your Number"
                ref="input"
                type="number"
                v-model="inputValue"
            />
            <div @click.stop="randomNumber" class="random btn">
                <span class="material-symbols-rounded"> casino </span>
            </div>
        </div>
        <div @click.stop="sendNumber" class="next btn">Next</div>
    </div>
</template>
<script>
import socketIO from "@/socket";
import LoaderComponent from "@/components/LoaderComponent.vue";

export default {
    beforeUnmount() {
        if (!this.safeChange) {
            this.socket.emit("game_abbandoned");
            this.$router.replace({ name: "start" });
        }
    },
    components: {
        LoaderComponent,
    },
    created() {
        this.socket = socketIO;
    },
    data() {
        return {
            inputValue: "",
            playerHasSelected: false,
            socket: null,
            safeChange: false,
        };
    },
    methods: {
        request() {
            this.$refs.input.disabled = true;

            this.socket.emit("select_number", this.inputValue, ({ status }) => {
                if (status === 200) {
                    this.safeChange = true;
                    this.$router.replace({ name: "game" });
                } else {
                    this.invalidToast();
                    this.$refs.input.disabled = false;
                    this.$refs.input.focus();
                }
            });
        },
        randomNumber() {
            if (this.$refs.input.disabled === true) return;
            this.inputValue = "";
            let arr = "0123456789".split("");
            while (this.inputValue.length < 4) {
                let index = Math.floor(Math.random() * arr.length);
                this.inputValue += arr[index];
                arr = arr
                    .slice(0, index)
                    .concat(arr.slice(index + 1, arr.length));
            }
        },
        writer(e) {
            e.stopPropagation();
            e.preventDefault();
            this.inputValue = String(this.inputValue);
            const { charCode, key } = e;
            if (
                47 < charCode &&
                charCode < 58 &&
                !this.inputValue.includes(key) &&
                this.inputValue.length < 4
            )
                this.inputValue += key;
        },
        eraser(e) {
            if (e.keyCode === 8) {
                e.stopPropagation();
                e.preventDefault();
                this.inputValue = this.inputValue.slice(
                    0,
                    this.inputValue.length - 1
                );
            } else return;
        },
        invalidToast() {
            const oldToast = document
                .getElementById("app")
                .querySelector(".toast");
            if (oldToast) oldToast.remove();

            const toast = document.createElement("div");

            toast.classList.add("toast");

            toast.innerHTML = `
				<span class="material-symbols-rounded toast_icon">cancel</span>
				<h3 class="toast_text">Invalid Number</h3>
				<div class="toast_timer"></div>
			`;

            document.getElementById("app").appendChild(toast);
            toast.style.left = `${
                document.getElementById("app").offsetWidth / 2 -
                toast.offsetWidth / 2
            }px`;
            setTimeout(() => {
                toast.remove();
            }, 1500);
        },
        sendNumber() {
            if (this.inputValue.length !== 4) this.invalidToast();
            else this.request();
        },
    },
    mounted() {
        this.socket.on("member_disconnected", () => {
            this.$router.replace({ name: "start" });
        });

        this.socket.on(
            "player_has_selected",
            () => (this.playerHasSelected = true)
        );
    },
    name: "SelectionComponent",
};
</script>
<style lang="scss" scoped>
.selection_container {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    position: relative;
    width: 100%;

    .loader_container {
        position: absolute;
        top: 100px;
        left: 50%;
        transform: translateX(-50%);

        h3 {
            background-color: #151515;
            padding: 10px 20px;
            border-radius: 10px;
        }

        &.loading {
            scale: 0.5;
        }
    }

    .input_container {
        align-items: center;
        display: flex;
        flex-direction: row;
        justify-content: center;
        margin: 50px 0 50px;
        position: relative;

        input {
            appearance: textfield;
            -moz-appearance: textfield;
            background-color: #151515;
            border-radius: 5px;
            border: none;
            color: #ccc;
            font-size: 1.3em;
            overflow: hidden;
            padding: 10px 45px 10px 20px;
            text-align: center;

            &:focus {
                animation: pulse 1s linear infinite;
                color: #fff;
                outline: none;
            }

            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button {
                -webkit-appearance: none;
            }
        }
        .random {
            align-items: center;
            color: #999;
            cursor: pointer;
            display: flex;
            height: 44px;
            justify-content: center;
            position: absolute;
            right: 0;
            width: 44px;

            &:hover > * {
                animation: heartBeat 1s linear infinite;
            }
        }
    }
    .next {
        background-color: rgb(80, 184, 80);
        border-radius: 5px;
        color: #fff;
        cursor: pointer;
        padding: 10px 20px;
        text-transform: uppercase;
        font-weight: bolder;

        &:hover {
            animation: heartBeat 1s linear infinite;
        }
    }
}
</style>
